import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { IndexService } from './../services/index.service';
import {Validators, FormBuilder,FormGroup,FormControl} from "@angular/forms";
import { AuthService } from './../../services/auth.service';




@Component({
  selector: 'app-more-detail',
  templateUrl: './more-detail.component.html',
  styleUrls: ['./more-detail.component.scss']
})
export class MoreDetailComponent implements OnInit {


  users:FormGroup;
  OBSLoginForm:FormGroup;
  moredetails={};
  show:boolean=false;
  questions:any;
  questionaries:any;

 
  constructor(private router:Router,private fb:FormBuilder,private sends:IndexService,private authService:AuthService) 
  { 
    
  }


  ngOnInit() 
  {

    this.users = this.fb.group({
      hiv: ['',Validators.required],
      protect : ['',Validators.required],
      protect2 : ['',Validators.required],
      protect3 : ['',Validators.required],
     
    });

    this.OBSLoginForm = this.fb.group({
    'username': [null,Validators.required],
    'password': [null,Validators.required]
    });

     // this.gettestcode();

     this.show=this.sends.commissioner;
    
   
     this.moredetails=JSON.parse(localStorage.getItem("component4"));
     
     if(this.moredetails)
     {
     this.users.controls.hiv.setValue(this.moredetails['quetionary1']['"When did you last have an HIV test?"']);
     this.users.controls.protect.setValue(this.moredetails['quetionary2']['"Have you had unprotected sex with in the last 12 months?"']);
     this.users.controls.protect2.setValue(this.moredetails['quetionary3']['"How many partners have you had sex with in the last 12 moths?"']);
     this.users.controls.protect3.setValue(this.moredetails['quetionary4']['"How often have you had sex under in influence of alchol or recreational drugs?"']);
     }
    

  }

  gettestcode()
  {
  this.questionaries=this.sends.commissioner;
  for(let i=0; i<=this.questionaries.testCodes.length;i++)
  {
    this.questions=this.questionaries.testCodes[i].testName;
    if(this.questions=='Syphilis')
    {
      this.show=true;
    }
   } 
   }


   LoginAction(formData:any){
     if(this.authService.loginAction(formData)){
     this.router.navigate(['dashboard']);
     }
   }

  datessend(formdata)
  {
    let requestobj={};
    requestobj=this.sends.seconddata;
    requestobj['quetionary1']={'"When did you last have an HIV test?"': this.users.value.hiv};
    requestobj['quetionary2']={'"Have you had unprotected sex with in the last 12 months?"': this.users.value.protect};
    requestobj['quetionary3']={'"How many partners have you had sex with in the last 12 moths?"': this.users.value.protect2};
    requestobj['quetionary4']={'"How often have you had sex under in influence of alchol or recreational drugs?"': this.users.value.protect3};
    

    /* local storage start */
    let obj={};
    obj['quetionary1']=requestobj['quetionary1'];
    obj['quetionary2']=requestobj['quetionary2'];
    obj['quetionary3']=requestobj['quetionary3'];
    obj['quetionary4']=requestobj['quetionary4'];

    

    localStorage.setItem('component4',JSON.stringify(obj));
    /* local storage end */
    this.router.navigate(['address']);
    console.log(this.sends.seconddata);

  }

  first()
  {
    this.router.navigate(['checkpostal']);
  }
  second()
  {
    this.router.navigate(['address']);
  }

  
  viewResult()
  {
    this.router.navigate(['viewresults']);
  }
  LoginPage()
  {
    this.router.navigate(['loginpageaction']);
  }

}
