  constructor(private DataService: DataService) {
    this.role = sessionStorage.getItem('userRole');
    this.getDashData();
    this.getSpendListforSponsor();
    this.getSuperAdminspendlistData();
  }
  getDashData() {
    //if(this.role="SPONSORUSER"){
    //}
    this.sponsorId = sessionStorage.getItem('sponsorId')
    if(this.sponsorId!=null){
    this.DataService.getDashboardData(this.sponsorId).subscribe(response => {
      this.dashData = response.resultData;
    }) 
  }
  else{
    this.DataService.getDashboardDataALl().subscribe(response => {
      this.dashDataAll = response.resultData;
    })
  }
  
  }
  getCommissionerSpentData(item){
    this.sponsorId=item;
    this.DataService.getsponsorDashboardspentList(this.sponsorId).subscribe(res=>{
      this.commissionerspent=res.resultData;
    });
  }
  getSpendListforSponsor() {
    this.sponsorId = sessionStorage.getItem('sponsorId');
    this.DataService.getsponsorDashboardspentList(this.sponsorId).subscribe(res=>{
      this.commissionerspentdata=res.resultData;
    })
  }
  getSuperAdminspendlistData() {
    this.sponsorId = sessionStorage.getItem('sponsorId');
    this.DataService.getSuperAdminspendlist().subscribe(res=>{
      this.superadminspenddata=res.resultData;
      //console.log("SUPERADMIN", this.superadminspenddata);
    })
  }
  dateoforderdashboard(){
    this.DataService.getreports().subscribe(res =>{
      this.reportsData=res.resultData;
      //console.log("Reports Data",this.reportsData[0].orderRequestsCount);
      for(let i=0;i<=this.reportsData.length;i++){
        this.dailyreport.data.datasets.forEach((dataset) => {
          this.dailyreport.data.labels.push(this.reportsData[i].dateOfOrder);
          dataset.data.push(this.reportsData[i].orderRequestsCount);
        });
        this.dailyreport.update();
       // console.log("Looped Data", this.reportsData[i].orderRequestsCount);
        this.reportsRequestCount.push(this.reportsData[i].orderRequestsCount);
        
      }
    })
    //console.log(this.reportsRequestCount);
  }
  testNameDashboardReports(){
    this.DataService.getreportsTests().subscribe(res =>{
      this.TestreportsData=res.resultData;
      //console.log("Test Reports Data",this.TestreportsData[0].orderRequestsCount);
      for(let i=0;i<=this.TestreportsData.length;i++){
        this.pieChartDemo.data.datasets.forEach((dataset) => {
          this.pieChartDemo.data.labels.push(this.TestreportsData[i].tsetName);
          dataset.data.push(this.TestreportsData[i].orderRequestsCount);
        });
        this.pieChartDemo.update();
        this.reportsRequestCount.push(this.TestreportsData[i].orderRequestsCount);
        
      }
    })
  }
  
