: 100%;\n  height: auto; }\n.profile-comments__body {\n  overflow: hidden; }\n.profile-comments__sender {\n  color: #333333;\n  font-weight: 500;\n  margin: 5px 0; }\n.profile-comments__sender > small {\n  margin-left: 5px;\n  font-size: 12px;\n  font-weight: 400;\n  color: #999999; }\n@media (max-width: 767px) {\n  .profile-comments__sender > small {\n    display: block;\n    margin: 5px 0 10px; } }\n.profile-comments__content {\n  color: #999999; }\n/*** Profile: Contact ***/\n.profile__contact-btn {\n  padding: 12px 20px;\n  margin-bottom: 20px; }\n.profile__contact-hr {\n  position: relative;\n  border-color: rgba(0, 0, 0, 0.1);\n  margin: 40px 0; }\n.profile__contact-hr:before {\n  content: \"OR\";\n  display: block;\n  padding: 10px;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  -webkit-transform: translate(-50%, -50%);\n  transform: translate(-50%, -50%);\n  background-color: #f5f5f5;\n  color: #c6c6cc; }\n.profile__contact-info-item {\n  margin-bottom: 30px; }\n.profile__contact-info-item:before,\n.profile__contact-info-item:after {\n  content: \" \";\n  display: table; }\n.profile__contact-info-item:after {\n  clear: both; }\n.profile__contact-info-item:before,\n.profile__contact-info-item:after {\n  content: \" \";\n  display: table; }\n.profile__contact-info-item:after {\n  clear: both; }\n.profile__contact-info-icon {\n  float: left;\n  font-size: 18px;\n  color: #999999; }\n.profile__contact-info-body {\n  overflow: hidden;\n  padding-left: 20px;\n  color: #999999; }\n.profile__contact-info-body a {\n  color: #999999; }\n.profile__contact-info-body a:hover,\n.profile__contact-info-body a:focus {\n  color: #999999;\n  text-decoration: none; }\n.profile__contact-info-heading {\n  margin-top: 2px;\n  margin-bottom: 5px;\n  font-weight: 500;\n  color: #999999; }\n\r\n/*# sourceMappingURL=data:application/json;base64, */"