import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { IndexService } from './../services/index.service';
import {Validators, FormBuilder,FormGroup,FormControl} from "@angular/forms";
import { AuthService } from './../../services/auth.service';


declare var $: any;

@Component({
  selector: 'app-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.scss']
})
export class AddressComponent implements OnInit {


  users:FormGroup;
  addressDetails: any;
  OBSLoginForm:FormGroup;
  detail:string="next";
  contact=true;
  first_box:boolean=false;
  second_box:boolean=false;
  third_box:boolean=false;
  lsaddress={};
  someobj={};
  dateArrival:any;
  olddate:any;
  reqdate:any;
  lsage:any;
  getyear:any;
  upyear:any;
  newyear:any;


  constructor(private router:Router,private fb:FormBuilder,private sends:IndexService,private authService:AuthService,private activatedRoute: ActivatedRoute) 
  { 
    
  }


  ngOnInit() 
  {


  $(document).ready(function(){
  $(".icon_3").click(function(){
  $(".inside_3").toggle();
  });
  });


  /* age validation start */
  this.lsage=JSON.parse(localStorage.getItem('component1'));
  this.olddate=new Date();
  this.reqdate=this.olddate.getFullYear();
  this.upyear=this.reqdate-this.lsage.age;
  this.newyear=JSON.stringify(this.upyear);
  this.dateArrival = new Date(this.newyear);
  /* age validation end */

 
 
  this.users = this.fb.group
  ({
  firstName: ['',[Validators.required,Validators.pattern('^[a-zA-Z \-\']+')]],
  lastName : ['',[Validators.required,Validators.pattern('^[a-zA-Z \-\']+')]],
  dob : ['',Validators.required],
  address :['',Validators.required],
  notificationPhone : ['',[Validators.required,Validators.maxLength(16),Validators.minLength(6),Validators.pattern('[^a-zA-Z \-\']+')]],
  notificationEmail : ['',[Validators.required,Validators.pattern('[A-Za-z0-9._%-]+@[A-Za-z0-9._%-]+\\.[a-zA-Z]{2,3}')]],
  lastResortLetter : ['',Validators.required]
  });

 
 
  /* local storage start */
  this.lsaddress=JSON.parse(localStorage.getItem("component5"));
  if(this.lsaddress)
  {
    this.first_box=true;
    this.second_box=true;
    this.third_box=true;
  }
  this.users.controls.firstName.setValue(this.lsaddress['firstName']);
  this.users.controls.lastName.setValue(this.lsaddress['lastName']);
  this.users.controls.dob.setValue(this.lsaddress['dob']);
  this.users.controls.address.setValue(this.lsaddress['address']);
  this.users.controls.notificationPhone.setValue(this.lsaddress['notificationPhone']);
  this.users.controls.notificationEmail.setValue(this.lsaddress['notificationEmail']);
  this.users.controls.lastResortLetter.setValue(this.lsaddress['lastResortLetter']);
  /* local storage end */


  this.someobj=this.sends.seconddata;

  if(JSON.parse(localStorage.getItem("edit_address"))==2)
  {

     if(!this.someobj['notificationPhone'])
     {
       this.second_box=true;
       this.third_box=true;
     }
     if(!this.someobj['notificationEmail'])
     {
       this.first_box=true;
       this.third_box=true;
     }
     if(this.someobj['notificationPhone'] && this.someobj['notificationEmail'] && this.someobj['lastResortLetter'])
     {
      this.first_box=true;
      this.second_box=true;
      this.third_box=true;
     }
      
     localStorage.removeItem("edit_address");
    
  }

  }



  LoginAction(formData:any)
  {
    if(this.authService.loginAction(formData)){
     this.router.navigate(['dashboard']);
  }
  }

 
  text()
  {
    this.first_box=!this.first_box;
  }
  
  text2()
  {
    this.second_box=!this.second_box;
  }
  
  text3()
  {
    this.third_box=!this.third_box;
  }

  

  


  datesends(formdata)
  {
    this.contact=false;
    let requestobj={};
    let obj={};
    requestobj=this.sends.seconddata;
    requestobj['dob']=this.users.value.dob;
    requestobj['firstName']=this.users.value.firstName;
    requestobj['lastName']=this.users.value.lastName;
    requestobj['address']=this.users.value.address;
    requestobj['notificationEmail']=this.users.value.notificationEmail;
    requestobj['notificationPhone']=this.users.value.notificationPhone;
    requestobj['lastResortLetter']=this.users.value.lastResortLetter;
    this.sends.seconddata=requestobj;
    /* local storage start */
    obj['dob']=this.users.value.dob;
    obj['firstName']=this.users.value.firstName;
    obj['lastName']=this.users.value.lastName;
    obj['address']=this.users.value.address;
    obj['notificationEmail']=this.users.value.notificationEmail;
    obj['notificationPhone']=this.users.value.notificationPhone;
    obj['lastResortLetter']=this.users.value.lastResortLetter;
    localStorage.setItem('component5',JSON.stringify(obj));
    /* local storage end */
    this.router.navigate(['edit']);
  }

  first()
  {
    this.router.navigate(['moredetail'])
  }

  getAddressDetails()
  {
    this.sends.lookup().subscribe(data=>{
    this.addressDetails=data.addresses;
    console.log(this.addressDetails)
  })
  }

  back()
  {
    this.router.navigate(['moredetail']);
  }

  viewResult()
  {
    this.router.navigate(['viewresults']);
  }
  LoginPage()
  {
    this.router.navigate(['loginpageaction']);
  }

  



}