import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';

declare var $: any;

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {

  constructor(private authService:AuthService,private routerNavigate:Router) { }

  ngOnInit() {

    $(document).ready(function(){

      $(".link_3").click(function(){
        $(".Offcanvas_menu_wrapper").addClass("active");
      });
      $(".canvas_close").click(function(){
        $(".Offcanvas_menu_wrapper").removeClass("active");
      });

    });

  }

  LoginPage()
  {
    this.routerNavigate.navigate(['loginpageaction'])
  }

  viewResult()
  {
    this.routerNavigate.navigate(['viewresults']);
  }





}
